<script setup lang="ts">
import { useRouteQuery } from "@vueuse/router";

const { t } = useI18n();
const searchValue = useRouteQuery<string>("q", "");

const componentKey = ref(0);
const forceUpdate = () => componentKey.value++;
const clearSearch = () => {
  searchValue.value = "";
};

definePageMeta({ layout: "home" });
useHead({ title: t("labels.archive") });
</script>

<template>
  <div class="main-container">
    <div class="flex justify-between gap-20 items-center pt-5 pb-10">
      <h1 class="text-xl sm:text-3xl font-bold text-main">
        {{ t('labels.archive') }}
      </h1>
      <BaseInput
        v-model="searchValue"
        class="font-medium"
        autocomplete="off"
        name="search"
        search
        :placeholder="t('labels.search')"
        @clear="clearSearch()"
        @keyup.enter="forceUpdate"
      />
    </div>

    <ListIdentities
      :key="componentKey"
      :search-value="searchValue"
      is-archived
      has-load-more-button
      class="mb-10"
    />
  </div>
</template>
